import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InputOutputInventoryComponent } from './input-output-inventory.component';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';

const routes: Routes = [
  {
    path: '',
    component: InputOutputInventoryComponent
  },
  {
    path: 'detail',
    loadChildren: () => import('./detail/detail.module').then(m => m.DetailModule)
  }
]

@NgModule({
  declarations: [InputOutputInventoryComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule
  ]
})
export class InputOutputInventoryModule { }
